[type='color'],
[type='date'],
[type='datetime-local'],
[type='datetime'],
[type='email'],
[type='month'],
[type='number'],
[type='password'],
[type='search'],
[type='tel'],
[type='text'],
[type='time'],
[type='url'],
[type='week'],
textarea,
select {
	font-size: 14px;
}
.the_post_content {
	&,
	p,
	div,
	pre,
	blockquote,
	li,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		font-size: 16px;
		line-height: 1.4;
		font-family: 'Montserrat', sans-serif;
		font-weight: normal;
		color: #4a4a4a;
		padding: 0;
		text-rendering: optimizeLegibility;
	}
	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin: 8px 0 16px;
		font-weight: 800;
		text-transform: uppercase;
	}
	p {
		margin-bottom: 12px;
	}
	strong,
	b {
		font-weight: 700;
	}
	h1 {
		font-size: 24px;
	}
	h2 {
		font-size: 20px;
	}
	h3 {
		font-size: 16px;
	}
	h4 {
		font-size: 15px;
	}
	h5 {
		font-size: 14px;
	}
	h6 {
		font-size: 12px;
		color: #1c2e36;
	}
	ul {
		list-style: disc;
	}
	ol {
		list-style: decimal;
	}
	a {
		color: #1d92cf;
	}
	blockquote {
		margin: 0 0 1rem;
		padding: 0.5625rem 1.25rem 0 1.1875rem;
		border-left: 1px solid #cacaca;
		color: #8a8a8a;
	}
	blockquote p,
	blockquote div,
	blockquote pre {
		color: #8a8a8a;
	}
}

