.board_page .accordion .toggle_btn {
	width: 58.33333%;
	padding-left: 16px;
	top: -46px;
	padding-top: 0;
	height: 46px;
	line-height: 46px;
	background: #f3f3f3;
	transition: all 0.25s ease;
	@media screen and (max-width: 640px) {
		width: 100%;
	}

	&:after {
		content: '\02795';
		font-size: 13px;
		color: white;
		float: right;
		margin-left: 5px;
		width: 40px;
		text-align: center;
	}
	&.opened:after {
		content: '\2796';
	}
	&.opened {
		background: #ebebeb;
	}
	&:hover {
		color: #1c2e36;
		background: #ebebeb;
	}
	&:hover:after {
		color: #5287b0 !important;
	}
}

