.advertising-cell {
	aside {
		position: relative;
	}
	#advertising {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}

	@media screen and (max-width: 1024px) {
		aside {
			margin-top: 0;
		}
		#advertising {
			position: relative;
			display: flex;
			aside {
				margin-top: 20px;
				margin-bottom: 0;
			}

			aside:not(:last-of-type) {
				margin-right: 20px;
			}
		}
	}

	@media screen and (max-width: 1024px) {
		#advertising {
			aside:not(:last-of-type) {
				margin-right: 10px;
			}
		}
	}
}

